input[type=range] {
  -webkit-appearance: none;
  margin-bottom: 11px;
  border: 0;
}
input[type=range]::-webkit-slider-runnable-track {
  height: 11px;
  background: #4a4a4a;
}
input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: 1px solid #fff;
  height: 11px;
  width: 13px;
  background: #fff;
}
input[type=range]:focus {
  outline: none;
}
input[type=range]::-moz-range-track {
  height: 11px;
  background: #4a4a4a;
  border: none;
  border-radius: 0;
}
input[type=range]::-moz-range-progress {
  height: 11px;
  background: #3c9cd6;
}
input[type=range]::-moz-range-thumb {
  border: none;
  height: 11px;
  width: 13px;
  background: #fff;
  border-radius: 0;
}
input[type=range]::-moz-focus-inner {
  border: 0;
}
input[type=range]::-ms-track {
  height: 11px;
  background: transparent;
  border-color: transparent;
  border-width: 6px 0;
  color: transparent;
}
input[type=range]::-ms-fill-lower {
  background: #3c9cd6;
  border-radius: 0;
}
input[type=range]::-ms-fill-upper {
  background: #4a4a4a;
  border-radius: 0;
}
input[type=range]::-ms-thumb {
  border: none;
  height: 11px;
  width: 13px;
  background: #fff;
  border-radius: 0;
}
::selection {
  color: #212121;
  background: #fff;
}
.jumbotron ::selection {
  color: #fff;
  background: #212121;
}
.text-primary::selection,
.text-primary a::selection {
  background: #3c9cd6;
}
.text-success::selection,
.text-success a::selection {
  background: #80c342;
}
.text-info::selection,
.text-info a::selection {
  background: #00b3b5;
}
.text-warning::selection,
.text-warning a::selection {
  background: #faa61a;
}
.text-danger::selection,
.text-danger a::selection {
  background: #ed1c24;
}
::-webkit-scrollbar {
  height: 15px;
  width: 15px;
}
::-webkit-scrollbar-thumb {
  background-color: #b7b7b9;
}
::-webkit-scrollbar-thumb:hover {
  background-color: #b7b7b9;
}
::-webkit-scrollbar-thumb:active {
  background-color: #5b5a5c;
}
::-webkit-scrollbar-track {
  background-color: #f1f1f1;
}
::-webkit-scrollbar-corner {
  background-color: transparent;
}
::-webkit-scrollbar-button {
  background-color: #f1f1f1;
}
::-webkit-scrollbar-button:active {
  background-color: #5b5a5c;
}
